<template>

    <div class="mypanel">
        <div class="hd">

            <div v-if="actiontype=='save'">新建优惠劵</div>
            <div v-if="actiontype=='update'">编辑优惠劵</div>

        </div>


        <table class="smart-table" border="1" cellspacing="1" width="100%">


            <tr>
                <td width="10%" class="tlabel" align="right">优惠劵名称:</td>
                <td width="*">
                    <input name="name" v-model="coupon.name" placeholder="优惠劵名称"
                           v-validate="{required:true,messages:{required:'请输入优惠劵名称'}}" class="input-txt" type="text"/>


                </td>
            </tr>


            <tr>
                <td width="10%" class="tlabel" align="right">面值:</td>
                <td width="*">
                    <input name="fee" v-model="coupon.fee" placeholder="面值"
                           v-validate="{required:true,messages:{required:'请输入面值'}}" class="input-txt" type="text"/>


                </td>
            </tr>


            <tr>
                <td width="10%" class="tlabel" align="right">最小金额:</td>
                <td width="*">
                    <input name="minreq" v-model="coupon.minreq" placeholder="最小金额"
                           v-validate="{required:true,messages:{required:'请输入最小金额'}}" class="input-txt" type="text"/>


                </td>
            </tr>


            <tr>
                <td width="10%" class="tlabel" align="right">过期日期:</td>
                <td width="*">
                    <el-date-picker v-model="coupon.overdate" format="yyyy-MM-dd" value-format="yyyy-MM-dd" type="date"
                                    placeholder="选择日期">
                    </el-date-picker>
                </td>
            </tr>


            <tr>
                <td colspan="4">

                    <el-button type="primary" icon="el-icon-check" @click="submitHandler">提交</el-button>

                </td>
            </tr>


        </table>


    </div>

</template>

<script type="text/javascript">

    import {VueUtil} from "../util/vueutil";


    export default {
        name: "couponadd",
        data() {
            return {
                actiontype: 'save',
                hostHead: this.$app.contextPath,
                fileList: [],
                errors: {},
                "coupon": {


                    "name": "",


                    "fee": "",


                    "minreq": "",


                    "overdate": new Date().format("yyyy-MM-dd"),


                },
            }
        },
        methods: {
            //begin load
            async load() {
                let id = this.$route.query.id;

                if (id != null) {
                    this.actiontype = "update";
                    this.pageTitle = "编辑优惠劵";
                    let url = "admin/coupon/load";
                    let util = new VueUtil(this);
                    let {data: res} = await util.http.post(url, {id: id});
                    console.log("res", res);
                    if (res != null && res.data != null) {
                        this.coupon = res.data;


                    }
                }
            },//end load
            async submitHandler() {

                let defaultOptions = {
                    url: "admin/coupon/save",
                    actionTip: "优惠劵新增成功"
                };
                if (this.actiontype == "update") {
                    defaultOptions.url = "admin/coupon/update";
                    defaultOptions.actionTip = "优惠劵更新成功";
                }
                const validRes = this.myValidator.valid(this);
                console.log("valRes", validRes);
                if (!validRes)
                    return;


                let util = new VueUtil(this);
                let params = {...this.coupon};
                let {data: res} = await util.http.post(defaultOptions.url, params);
                if (res.stateCode <= 0) {
                    util.alert(res.des, '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }
                this.$message.success(defaultOptions.actionTip);

                this.$router.push("/admin/couponmanager");

            },


        },
        created() {
            this.load();
        }

    }

</script>
